import React from "react"; 
import { Link } from "react-router-dom";
import "./Login.css";
// react-bootstrap
import Form from "react-bootstrap/Form";
import FormData from 'form-data';
import Button from "react-bootstrap/Button";
import { defaultLink } from "../constants";

const formData = new FormData();

console.log(formData);

class MyComponent extends React.Component {
    constructor(props) {
      super(props);
      this.myRef = React.createRef();  }
    render() {
      return <div ref={this.myRef} />;  }
  }

function export_JSON() {
    //window.event.preventDefault()
    const i_email = formData.enteries("formBasicEmail")
    const i_password = formData.enteries("formBasicPassword")
    
    let xhr = new XMLHttpRequest();
    let url = "https://students.cs.niu.edu/~z1860207/Group3B/submit.php";

    xhr.open("POST", url, true);

        // Set the request header i.e. which type of content you are sending
        xhr.setRequestHeader("Content-Type", "application/json");
    
        // Create a state change callback
        xhr.onreadystatechange = function () {
            console.log(xhr.readyState);
            console.log("ready state | status seperation string")
            console.log(xhr.status);
        };

    var user_credentials = JSON.stringify({"email": i_email, "password": i_password});
    xhr.send(user_credentials);       
}
function Login() {     
    return (  
        <div className="login">
            <Link to={defaultLink} style={{ textDecoration: "none" }}>
                <h1 className="login__logo">Logo</h1>
            </Link>
            <div className="login__border">
                <Form className="login__form">
                    <Form.Group className="mb-3" controlId="formBasicEmail">
                        <Form.Label>Email Address</Form.Label>
                        <Form.Control type="email" ref={this.i_email} placeholder="Enter email" />
                    </Form.Group>
                    {/* Password input */}
                    <Form.Group className="mb-3" controlId="formBasicPassword">
                        <Form.Label>Password</Form.Label>
                        <Form.Control type="password" ref={this.i_password} placeholder="Password"/>
                    </Form.Group>
                    {/* Save Login Details Checkbox */}
                    <Form.Group className="mb-3" controlId="formBasicCheckbox">
                        <Form.Check type="checkbox" label="Remember Me" />
                    </Form.Group>
                    <div className="login__signInButtons">
                        <div className="login__button">
                            <Button variant="primary" type="submit" controlID="login_btn">
                                Sign In
                            </Button>
                        </div>
                        <div className="login__button">
                            <Button variant="primary" type="submit" controlId="create_btn">
                                Create Account
                            </Button>
                        </div>
                    </div>
                </Form>   
            </div>
        </div>
    );
}
export default Login;